	import {
			mapState,  
		    mapMutations  
		} from 'vuex'; 
export default {
	data() {
		return {
			active: false
		};
	},
	computed: {
		...mapState(['token', 'globalUserInfo'])
	},
	onLoad() {},
	onShow() {
		var that = this;
		that.checkLocalInfo();
		// setTimeout(() => {
		this.active = true;
		// }, 500);
	},
	onHide() {
		this.active = false;
	},
	methods: {
		checkLocalInfo() {
			var that = this;
			if(that.token == null){
				setTimeout(() => {
					uni.showModal({
						title: '提示',
						content: '您还未登录，登录体验更多功能',
						cancelText: '先逛逛',
						confirmText: '去登录',
						success: function(res) {
							if (res.confirm) {
								uni.navigateTo({
									url: '/pages/login/login'
								});
							} else if (res.cancel) {}
						}
					});
				}, 800)
				uni.hideLoading();
			} else {
				uni.hideLoading();
			}
		},
		goToPage(url) {
			if (this.token==null){
				uni.showToast({
					title: "登录才能使用此功能",
					icon: 'none'
				})
				return false;
			}
			if (!url) return;
			uni.navigateTo({
				url
			});
		}
	}
};
